<template>
    <div>
        <div class="add-page new-menus">
            <div class="row">
                Type
                <select name="pageType" id="pageType" v-model="pagetype">
                    <option value="" selected>Select Page Type</option>
                    <option value="page">Page</option>
                    <option value="custom">Custom Link</option>
                </select>
            </div>
            <!-- FOR PAGE TYPE -->
            <div class="row" v-if="pagetype == 'page'">
                Page
                <select name="pageid" id="pageid" v-model="pageid">
                    <option v-for="(page, i) in pages" :key="i" :value="page.page_id">
                        {{page.title}}
                    </option>
                </select>
            </div>
            <!-- FOR CUSTOM MENU -->
            
            <div class="row" v-if="pagetype == 'custom'">
                Label
                <input type="text" name="customlabel" id="customlabel" v-model="customlabel"/>
            </div>
             <div class="row" v-if="pagetype == 'custom'">
                URL
                <input type="text" name="customurl" id="customurl" v-model="customurl"/>
            </div>

            <div class="row">
                <button v-on:click="add_page">Add Page</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            selected_pages: [
                { 
                    page: 0,
                    type: "",
                    label: "",
                    css: "",
                    url: "",
                    structure: []
                }
            ],
            pagetype: "",
            pageid: 0,
            customlabel: "",
            customurl: ""
        }
    },
    props: {
        pages: {
            required: true,
            type: Array
        }
    },
    methods: {
        add_page: function(){
            let page_title, url;
            if ( this.pagetype == 'page' ){
                page_title = this.pages.find(x => x.page_id == this.pageid).title;   // GET THE TITLE FROM PAGES JSON
                url = "";
            }
            if ( this.pagetype == 'custom' ){
                page_title = this.customlabel;   // GET THE TITLE CUSTOM'S LABEL
                url = this.customurl;
            }
            this.selected_pages = {
                page: this.pageid,
                type: this.pagetype,
                label: page_title,
                css: "",
                structure: [],
                url: url
            };
            this.$emit("selected_pages", this.selected_pages);
        }
    }
}
</script>

<style>
    .new-menus .row{ width: 100%; margin-bottom: 30px; }
</style>