l<template>
	<draggable class="dragArea" tag="ul" :list="menus" :group="{ name: 'g1' }" v-bind="dragOptions" @start="drag = true" @end="drag = false">
		<li class="dragArea menus-lists list-group-item" v-for="(el, index) in menus" :key="index">
			<expand :allmenu="menus" :item-data="el"></expand>
			<nested-draggable :menus="el.structure" v-if="el.structure"/>
		</li>
	</draggable>
</template>
<script>
	import draggable from "vuedraggable";
	import expand from '@/components/expand.vue';

	export default {
		data() {
			return {	
				transitionName: 'fade',
				active: false,
				toggled: false,
			}
		},
		components: {
			draggable,
			expand
		},		
		props: {
			menus: {
				required: true,
				type: Array
			}
		},
		name: "nested-draggable",
		computed: {
			dragOptions() {
				return {
					animation: 200,
					group: "description",
					disabled: false,
					ghostClass: "ghost"
				};
			},
		},
		methods: {
			toggleItem: function() {
				this.toggled = !this.toggled;
			}
		},
		created () {
			
		}
	};
</script>
<style scoped>
	/* .dragArea {
		min-height: 50px;
		outline: 1px dashed;
	} */
	
	.list-group-item:first-child {
		border-top-left-radius: .25rem;
		border-top-right-radius: .25rem;
	}
	.list-group-item {
		position: relative;
		display: block;
		padding: .75rem 1.25rem;
		margin-bottom: -1px;
		background-color: #fff;
		/* border: 1px solid rgba(0,0,0,.125); */
	}
	.list-group-item {
		cursor: move;
	}
	li > ul > li.menus-lists.list-group-item {
		padding-right: 0;
	}
	.expand-transition {
		transition: all .3s ease;
		height: 30px;
		padding: 10px;
		background-color: #eee;
		overflow: hidden;
	}

	/* .expand-enter defines the starting state for entering */
	/* .expand-leave defines the ending state for leaving */
	.expand-enter,
	.expand-leave {
		height: 0;
		padding: 0 10px;
		opacity: 0;
	}
</style>