<template>
    <div>
        <div class="box">
            <div class="menu-title">{{ itemData.label }}</div>
            <div class="menu-type">{{ itemData.type }}</div>
            <button @click="toggleItem" class="expand-button">&#8595;</button>
        </div>
        <div class="body" v-show="toggled" transition="expand">
            <div class="form-container">
                <div class="row">
                    Label <input type="text" name="" id="" v-model="itemData.label">
                </div>
                <div class="row">
                    CSS Class <input type="text" name="" id="" v-model="itemData.css">
                </div>
                <div class="row" v-if="itemData.type == 'custom'">
                    URL <input type="text" name="" id="" v-model="itemData.url">
                </div>
                <button @click="removeMenu(itemData)">Remove Menu</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
            itemData: {
                required: true
            },
            allmenu: {
                required: true
            }
    },
    data() {
         return {
            toggled: false,
        }
    },
    methods: {
        toggleItem: function () {
            this.toggled = !this.toggled;
        },
        removeMenu(arr){
            console.log(this.allmenu);
            this.allmenu.splice(arr, 1);
        }
    }
}
</script>
<style scoped>
	.box {
		border: 1px solid #ccc;
		border-radius: 4px;
		padding: 15px;
		display:flex;
	}
	.box > div { width: 50%; text-transform: capitalize; }
    
	.menu-type { text-align: right; }
	.expand-button{ margin-left: 3px; }
    .row{ width: 100%; margin-bottom: 30px; }
    .form-container {
        padding: 20px;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
    }
    .form-container input[type=text]{
        width: 100%;
        margin-top: 5px;
        padding: 5px;
    }
</style>